<template>
  <div class="crm-wrapper" >
    <!-- 主体 -->
    <div class="crm-main-wrapper">
      <!-- 导航 -->
      <el-menu @select="selectFn" :default-active="secondMenuActive" router mode="horizontal" class="el-menu-demo">
        <div v-for="(item,index) in menuChild[0].children" :key="index" class="crm-menu">
          <el-menu-item :index="item.path" :key="item.path" >
            <span >{{ item.name }}</span>
          </el-menu-item>
          <div class="crm-menu-line" v-if="!(index===4)">|</div>
        </div>
      </el-menu>
    </div>
    <!-- 导航 /-->
    <!-- 内容部分 -->
    <div class="crm-main-container">
      <!-- 路由 -->
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
      <!-- 路由/ -->
    </div>
    <!-- 内容部分 /-->
    <!-- 主体 /-->
  </div>
</template>
<script>
  import menu from '@/router/menu'
  export default {
    name: 'crm',
    data () {
      return {
        menu: menu // 导航菜单
      }
    },
    created () {
    },
    computed: {
      state () {
        return this.$store.state.app
      },
      menuChild: function () { // 标签过滤数组
        return this.menu.filter(function (number) {
          if (number.path === '/crm') {
            return number
          }
        })
      },
      secondMenuActive () {
        if (this.$route.path.search('Detail') !== -1) {
          return this.$route.path.split('Detail')[0]
        } else {
          return this.$route.path
        }
      }
    },
    mounted () {
    },
    methods: {
      /**
       * 选择菜单
       */
      selectFn (a) {
//        console.log(a, this.$route.path)
//
//        this.$router.push({
//          path: a
//        })
      }
    }
  }
</script>





